.container {
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
  }
  
  .review-board {
      margin-top: 20px;
      padding: 200px;
      border: 1px solid #ccc;
      border-radius: 5px;
    }
    
    .review-board h2 {
    font-size: 18px;
    font-weight: bold;
    margin-bottom: 10px;
    margin-top: -150px;
    }
    .new-post {
      margin-bottom: 10px;
    }
    
    .new-post input[type="text"] {
      width: 300px;
      padding: 5px;
      margin-right: 10px;
    }
    
    .new-post button {
      padding: 5px 10px;
      background-color: #007bff;
      color: white;
      border: none;
      cursor: pointer;
    }
    
    .new-post button:hover {
      background-color: #0056b3;
    }
    
    .post-list {
      list-style-type: none;
      padding: 0;
    }
    
    .post-list li {
      border: 1px solid #eee;
      border-radius: 5px;
      padding: 10px;
      margin-bottom: 10px;
    }
    
    .post-list li h3 {
      font-size: 18px;
      margin-bottom: 5px;
    }
    
    .post-list li button {
      padding: 3px 6px;
      background-color: #dc3545;
      color: white;
      border: none;
      cursor: pointer;
      float: right;
    }
    
    .post-list li button:hover {
      background-color: #c82333;
    }
    
    .write-button {
      position: fixed;
      bottom: 20px;
      right: 20px;
      z-index: 1000;
    }
    
    .input-container {
      margin-top: 20px;
    }
    
    
    .post {
      margin-top: 20px;
    }
    
    .review button {
      padding: 5px 10px;
      margin: 5px;
      background-color: #007bff;
      color: white;
      border: none;
      cursor: pointer;
    }
    
    .review button:hover {
      background-color: #0056b3;
    }
    