*{
  text-decoration: none; 
  list-style: none;
  color: black;
  }
  .App {
    text-align: center;
  }
  
  .App-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #282c34;
    padding: 20px;
    color: white;
  }
  
  .header-left {
    flex: 1;
  }
  
  .header-left h1 {
    margin: 0;
  }
  
  .header-left h1 a {
    color: white;
    text-decoration: none;
  }
  
  .header-center {
    flex: 2;
    display: flex;
    justify-content: center;
    margin-right: 250px;
  }
  
  .header-center nav {
    display: flex;
    gap: 20px;
  }
  
  .header-center li {
    display: flex;
    flex-direction: row;
    list-style: none;
    float: left;
  }
  .header-center a,
  .header-center button {
    color: white;
    background: none;
    border: none;
    cursor: pointer;
    font-size: 18px;
    text-decoration: none;
  }
  
  .header-right {
    display: flex;
    justify-content: flex-end;
    gap: 10px;
    padding: 10px;
    background-color: #61dafb;
    border: none;
    cursor: pointer;
    font-size: 18px;
    color: white;
  }
  
  .header-right h1 {
    margin: 0;
    font-size: 24px;
  }
  
  .header-right button {
    padding: 10px;
    background-color: #61dafb;
    border: none;
    cursor: pointer;
    font-size: 18px;
    color: white;
  }
  
  
  
  main {
    padding: 20px;
  }
  
  .search-section {
    margin: 20px auto;
    width: 300px; 
    display: flex; 
  }
  
  .search-section input {
    flex: 1; 
    padding: 10px;
    margin-right: 10px;
  }
  
  .search-section button {
    padding: 10px;
  
    cursor: pointer;
  }
  
  
  .image-slider {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 20px 0;
    width: 800px; 
    margin: 0 auto; 
  }
  
  .image-slider img {
    width: 100%;
    height: 300px;
    object-fit: cover;
    display: block;
  }
  
  .arrow {
    position: absolute;
    top: 0; 
    height: 300px;
    width: 30px;
    background-color: rgba(0, 0, 0, 0.5);
    color: white;
    border: none;
    cursor: pointer;
    z-index: 1;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .left-arrow {
    left: 0;
  }
  
  .right-arrow {
    right: 0;
  }
  
  .book-list {
    list-style-type: none;
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
  }
  
  .book-item {
    display: flex;
    align-items: flex-start;
    margin-bottom: 20px;
  }
  
  .book-thumbnail {
    max-width: 100px;
    margin-right: 0;
    margin-bottom: 10px; 
  }
  
  .book-info {
    flex: 1;
  }
  
  .book-info h2 {
    margin-top: 10;
  }
  
  .book-info p {
    margin: 5px 0;
  }
  
  .example-book-list {
    text-align: left;
  }
  
  .example-book-list h2 {
    margin-top: 40px;
    margin-left: 300px;
  }
  
  .example-book-list ul {
    list-style-type: none;
    padding: 0;
  }
  
  .example-book-list li {
    margin: 20px 0;
    border-bottom: 1px solid #ccc;
    padding-bottom: 10px;
    display: flex;
    align-items: center;
  }
  
  .example-book-list li img {
    max-width: 100px;
    margin-right: 30px;
    margin-left: 300px;
  }
  
  .Books-list li .example-book-info {
    flex: 1;
  }
  
  header button#book-list-btn {
    background-color: #282c34;
    border-radius: 5px;
    color: white;
  }
  
  header button#book-list-btn:hover {
    background-color: rgba(255, 255, 255, 0.1);
  }
  
  .search-results h2 {
    margin-bottom: 20px;
    text-align: left; 
    margin-left: 300px; 
  }
  
  .search-results h2 a{
    margin-left: -300px; 
    margin-bottom: 20px;
  }
  
  
  .search-results img {
    margin-left: 300px;
  }
  
  .search-results .book-list {
    list-style-type: none;
    padding: 0;
  }
  
  .search-results .book-item {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
  }
  
  .search-results .book-thumbnail {
    max-width: 100px;
  }
  
  .search-results .book-info {
    margin-top: 10px;
    margin-left: 30px;
    text-align: left; 
  }
  
  .search-results .book-info h2 {
    margin-top: 0;
  }
  
  .search-results .book-info p {
    margin: 5px 0;
  }
  
  .container{
    border: 0;
    width: 300px;
    padding: 30px;
    border-radius:5px;
    background-color:#EEEFF1;
    margin-top: 100px;   
    margin-left: auto;
    margin-right: auto;
  }